import {useParams} from "react-router-dom";
import { useState, useEffect } from "react";
import axios  from "axios";
import LoadingArticle from "./loading/loadingArticle";
const Content = () =>{
    const {id} = useParams();
    const [article, setArticle] = useState({})
    const [loading, setLoading] = useState(true)

    document.title = article.title ? article.title : "Content"
    useEffect(() =>{
        async function result(){
            const response = await axios.get(`https://backendtest.pythonanywhere.com/api/article/${id}/`)
            setArticle(response.data)
            setLoading(false)
        }
        result()
    },[])

    return(
        <>

        {
            loading ? (
                <LoadingArticle/>
            ):(
               <>
                <h1>{article.title}</h1>
                <p>{article.content}</p>
            {article.picture ? (
                <img src={article.picture} alt="" width={200} height={200} />
        
            ):(
               <></>
            )}
                </>
            )
        }
        </>
    )
}

export default Content;