import { Link,NavLink } from "react-router-dom";
import { useState,useEffect } from "react";
const Navbar = (props) =>{
    const [authorization,setAuthorization] = useState(false)
    useEffect(()=>{
      const chackAuthorzation = () =>{
        if (!props.user){
          setAuthorization(false)
        }else{
          setAuthorization(true)
        }
      }
      chackAuthorzation()
    },[props])
    return (
        <>
        <nav className="navbar navbar-expand-lg bg-body-tertiary">
            <div className="container-fluid">
    <NavLink style={({isActive}) =>{
      return {color:isActive ? "black":"black"}
    }}  className="navbar-brand" to="/">Jetwrie FR 1.0.0 <span class="badge text-bg-warning">Beta</span>
</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarNav">
      <ul className="navbar-nav">
        <li className="nav-item">
          <NavLink  style={({isActive}) =>{
            return {color:isActive ? "#000":"#3b3b3b"}
          }} className="nav-link active" aria-current="page" to="/">Home</NavLink>
        </li>
        
     
         

        
        {
          !authorization ?(
            <>
               <li className="nav-item">
          <NavLink  style={({isActive}) =>{
            return {color:isActive ? "#000":"#3b3b3b"}
          }} className="nav-link active" aria-current="page" to="/login">login</NavLink>
          
        </li>
        <li className="nav-item">
          <NavLink  style={({isActive}) =>{
            return {color:isActive ? "#000":"#3b3b3b"}
          }} className="nav-link active" aria-current="page" to="/register">Register</NavLink>
          
        </li>
            </>
          ) : (
            <>
            <li className="nav-item">
          <NavLink  style={({isActive}) =>{
            return {color:isActive ? "#000":"#3b3b3b"}
          }} className="nav-link active" aria-current="page" to="/dashboard">Dashboard</NavLink>
          
        </li>
        <li className="nav-item">
          <NavLink  style={({isActive}) =>{
            return {color:isActive ? "#000":"#3b3b3b"}
          }} className="nav-link active" aria-current="page" to="/logout">Logout</NavLink>
          
        </li>
            
            </>
          )
        }
      </ul>
    </div>
  </div>
</nav>
        
        </>
    )
}
export default Navbar;