import React, { Component } from "react";
import axios from "axios";
import LoadingArticles from "./loading/loadingArticles";
import { Link } from "react-router-dom";
import { createRef } from "react";

class Home extends React.Component {
    state = {
        articles:[],
        isLoading:true,
        user:"",
        isError:false,
    }
   

    inputTitle = createRef()
    inputContent = createRef()
    InputUpdateTitle = createRef()
    InputUpdateContent = createRef()

    async componentDidMount(){
        const token = localStorage.getItem('token')
        if (!token){
            const response = await axios.get('https://backendtest.pythonanywhere.com/api/article/');
            this.setState({articles:response.data,isLoading:false});

        }else{
            const response = await axios.get('https://backendtest.pythonanywhere.com/api/article/',{
                headers:{
                    "Authorization":`Token ${token}`
                }
            }
            )
            console.log(response)

        this.setState({articles:response.data,isLoading:false});
           
        }
        axios.get("https://backendtest.pythonanywhere.com/api/auth/users/me/",{ 
            headers:{
                'Authorization':`Token ${token}`
            }
        }).then((response) =>{
            this.setState({
                user:response.data
            }
           

        )
        console.log(response)
        }).catch(()=>{
            localStorage.removeItem("token")
            this.setState({
                user:""
            })
        })
    }
    render(){
        document.title = "Jetwrie FR home"
        return (
            
            <>
            {
        this.state.isError ?(
            <>
            <div class="alert alert-danger" role="alert">
                Error
            </div>
            </>
        ):(
            <>
            </>
        )
    }
    {this.state.user === "" ?(
        <>
        
        
        </>
    ):(
        <>
        <button class="btn btn-primary" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasRight" aria-controls="offcanvasRight" style={{"position":"fixed"}}>Create</button>

        </>
    )}
    
    
        <div class="offcanvas offcanvas-end" tabindex="-1" id="offcanvasRight" aria-labelledby="offcanvasRightLabel">
            <div class="offcanvas-header">
                <h5 id="offcanvasRightLabel">Create Post</h5>
                <button type="button" class="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
            </div>
            <div class="offcanvas-body">
            <div class="mb-3">
            <label for="recipient-name" class="col-form-label" >Title:</label>
            <input type="text" class="form-control" id="recipient-name" ref={this.inputTitle}/>
          </div>
            <div class="mb-3">
            <label for="message-text" class="col-form-label">Content:</label>
            <input type="text" class="form-control" id="recipient-name" ref={this.inputContent}/>
          </div>
          <div class="modal-footer">
        <button type="button" class="btn btn-primary" onClick={() => this.handleCreate(this.state.user.id,this.state.user.username)} disabled={this.state.isLoading} >Send Post</button>
      </div>
            </div>
        </div>
            <div className="row">
                {
                    this.state.isLoading ?(
                        <LoadingArticles/>
                    ):(
                        
                            this.state.articles.map((article)=>{
                                return (
                                    <div className="col-4 text-center p-5">
                                        {
                                            article.picture ? (
                                                <img src={article.picture} alt="" style={{"width":"200"}} width={200} height={200}  />  

                                            ):(
                                                <></>
                                            )
                                            
                                        }
                                        <h1><Link to={`content/${article.id}`}>{article.title}</Link></h1>
                                        <h4>{article.content}</h4>
                                        <h6>
                                            {article.user.username}
                                        </h6>
                                        <div className="row">
                                            <div className="col-6">
                                            {this.state.user === "" ?(
        <>
        
        
        </>
    ):(
        <>
        {this.state.user.id === article.author ? (
            <>
              <button  type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasExample" aria-controls="offcanvasExample" className="btn btn-info btn-sm">
                                                Update
                                                </button>
            </>
        ):(<>
    
        </>)}
   
        </>
    )}
                                               
                                                <div class="offcanvas offcanvas-start" tabindex="-1" id="offcanvasExample" aria-labelledby="offcanvasExampleLabel">
  <div class="offcanvas-header">
    <h5 class="offcanvas-title" id="offcanvasExampleLabel">Update</h5>
    <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
  </div>
  <div class="offcanvas-body">
    <div>
    <label for="recipient-name" class="col-form-label" >Title:</label>
            <input type="text" class="form-control" id="recipient-namea" ref={this.InputUpdateTitle} />
          </div>
            <div class="mb-3">
            <label for="message-text" class="col-form-label">Content:</label>
            <input type="text" class="form-control" id="recipient-namea" ref={this.InputUpdateContent} />
          </div>
          <div class="modal-footer">
        <button type="button" class="btn btn-primary" onClick={() => this.handleUpdate(article.id,this.state.user.id,this.InputUpdateTitle,this.InputUpdateContent)} disabled={this.state.isLoading}>Send Update</button>
    </div>
  </div>
</div>

                                            </div>
                                            <div className="col-6">
                                            {this.state.user === "" ?(
        <>
        
        
        </>
    ):(
        <>
        {this.state.user.id === article.author ? (
            <>
            <button className="btn btn-danger btn-sm" disabled={this.state.isLoading} onClick={() => this.handleDelete(article.id)}>Delete</button>
            </>
        ):(<>
    
        </>)}
        

        </>
    )}
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                    )
                }
            </div>
            </>
        )
    }
    handleCreate = async (userId,username) =>{
        this.setState({isLoading:true});
        const newArticle = {
        "user": {
            "username": username,
            "first_name": "",
            "last_name": ""
        },
        "title":`${this.inputTitle.current.value}`,
        "content":`${this.inputContent.current.value}`,
        "date": "2024-04-17",
        "detaNow": "2024-04-17T11:01:40.216965Z",
        "picture": null,
        "author": `${userId}`,
        }
        const response = await axios.post('https://backendtest.pythonanywhere.com/api/article/',newArticle).then(()=>{
            this.componentDidMount()
        }).catch(()=>{
            this.setState({
                isError:true,
            })
        })
        this.setState({isLoading:false});

    }
    handleUpdate = async (id,userId,title,content) =>{
        this.setState({isLoading:true});
        const UpdateArticle = {
            "title":title.current,
            "content":content.current,
            "author":`${userId}`,
            }
        console.log(title.current.value)
        const response = await axios.put(`https://backendtest.pythonanywhere.com/api/article/${id}/`,UpdateArticle).then(()=>{
            this.componentDidMount()
        }).catch(()=>{
            this.setState({
                isError:true,
            })
        })
        
        
        this.setState({isLoading:false});

    }
    handleDelete = async (id) =>{
        this.setState({isLoading:true});
        const response = await axios.delete(`https://backendtest.pythonanywhere.com/api/article/${id}`)
        this.componentDidMount()
        this.setState({isLoading:false});
    }
}

export default Home;