import React, { Component } from "react";
import Navbar from "./components/navbar";
import Home from "./components/home";
import { Routes , Route,Navigate,redirect } from 'react-router-dom';
import Login from './components/login';
import Content from "./components/content";
import Register from "./components/register";
import Dashboard from "./components/dashboard";
import Logout from "./components/logout";
import NotFound from "./components/notfound";
import axios from "axios";


class App extends React.Component{
    state = {
        user:null,
        
    }
    async componentDidMount(){
        const token = localStorage.getItem("token")
        if(!token){
            this.setState({user:null});
            return;
        }else{
            axios.get("https://backendtest.pythonanywhere.com/api/auth/users/me/",{ 
                headers:{
                    'Authorization':`Token ${token}`
                }
            }).then((response) =>{
                if (response.status){
                    this.setState({user:response.data.username});
                }else{
                    localStorage.removeItem("token")
                }
            }).catch(()=>{
                this.setState({user:null});
                localStorage.removeItem("token")
            })
        }
        console.log("hello");
    }
    
    render(){
        setTimeout(()=>{
            this.componentDidMount()
        },5000)
        return (
            <>
            <Navbar user={this.state.user}/>
            <div className="container mt-3">
            <Routes>
                <Route path='/'  element={<Home />}/>
                <Route path='/home' element={<Navigate to="/"/>}/>
                <Route path='/login' element={<Login />}/>
                <Route path='/content' element={<Content />}/>
                <Route path='/content/:id' element={<Content />}/>
                <Route path='/register' element={<Register />}/>
                <Route path='/dashboard' element={<Dashboard   user={this.state.user} />}/>
                <Route path='/logout' element={<Logout />} user={this.state.user}/>
                <Route path='*' element={<NotFound/>} />
            </Routes>
            </div>
            </>
        )
    }
}

export default App;