import axios from "axios";
import { useRef,useState } from "react";
import {useNavigate} from "react-router-dom";
const Register = () => {
    document.title = "Register"
    
    const [user,setUser]= useState({})
    const username = useRef(null)
    const password = useRef(null)
    const navigate = useNavigate()
    const handelSubmit = async (e) =>{
        e.preventDefault();
        const account = {
            username:username.current.value,
            password:password.current.value,
        }
        if (account.username && account.password){
            if(account.password.length >= 4){
                if(account.username.length >= 4){
                    const response =  await axios.post('https://backendtest.pythonanywhere.com/api/auth/users/', account);
                    if (response){
                        const responseLogin =  await axios.post('https://backendtest.pythonanywhere.com/api/token/auth/', account);
                        localStorage.setItem("token",responseLogin.data.token)
                        if (responseLogin){
                            navigate('/')
                        }
                    }
                }
            }
        }
    }
        return (
            <form onSubmit={handelSubmit}>
                <h1>Register</h1>
                <div className="mb-3">
                    <label htmlFor="username">Username:</label>
                    <input ref={username} type="text" className="form-control"  id="username"/>
                </div>
                <div className="mb-3">
                    <label htmlFor="password">Password:</label>
                    <input ref={password}  type="text" className="form-control"  id="password"/>
                </div>
                <button className="btn btn-primary">Register</button>
            </form>
        )
}



export default Register;