import axios from "axios";
import { Component,createRef } from "react";
import * as yup from "yup"
import { useNavigate, Navigate} from 'react-router-dom'


class Login extends Component{
    state = {
        error:false,
        errorMsg: "",
        sending:false,
        submited:false,
    }
    username = createRef();
    password = createRef();
    
   
    handelSubmit = async (e)=>{
        e.preventDefault();
        console.log("username: " + this.username.current.value + " password: "+this.password.current.value);
        const account = {username:this.username.current.value,password:this.password.current.value}
        console.log(account);
        if (account.username && account.password){
            if(account.password.length >= 4){
                if(account.username.length >= 4){
                    try{
                        this.setState({
                            sending:true,
                        })
                         const response =  await axios.post('https://backendtest.pythonanywhere.com/api/token/auth/', account);
                         console.log(response.data.token);
                         localStorage.setItem("token",response.data.token)
                         const token = localStorage.getItem('token')
                        if (token){
                            this.setState({
                                errorMsg:"",
                                error:false,
                                sending:false,
                                submited:true,
                            })
                        }
                        //  window.location = '/'
                        
                    }catch(error){
                        console.log("Username or password is invaild");
                        this.setState({
                            errorMsg:"Username or Password is invaild",
                            error:true,
                            sending:false,
                        })
                    }
                }
                else{
                    this.setState({
                        errorMsg:"Error Username Or Password",
                        error:true
                    })
                }
            }else{
                this.setState({
                    errorMsg:"Password is invaild",
                    error:true
                })
            }
        }else{
            this.setState({
                errorMsg:"Username or Password field",
                error:true
            })
        }
    }

   
    
    render(){
    document.title = "Login"
        return (
            <>
            {
                this.state.error ? (
                    <div className="alert alert-danger">
                        {this.state.errorMsg}
                    </div>
                ):(
                    <></>
                )
            }
                
            <h1>Login</h1>
            <form onSubmit={this.handelSubmit}>
                <div className="mb-3">
                    <label htmlFor="username">Username:</label>
                    <input ref={this.username} type="text" className="form-control"  id="username"/>
                </div>
                <div className="mb-3">
                    <label htmlFor="password">Password:</label>
                    <input ref={this.password} type="text" className="form-control"  id="password"/>
                </div>
                <button disabled={this.state.sending}  className="btn btn-primary">Login</button>
            </form>
            {
                this.state.submited ? (
                    <>
                    <Navigate to="/"></Navigate>
                    </>
                ):(
                    <>

                    </>
                )
            }
            </>
        )
    }
}


export default Login;